import React, { useEffect } from "react";
import BannerSec from "../Components/Home-page/BannerSec";
import NewArrival from "../Components/Home-page/NewArrival";
import MaterialSec from "../Components/Home-page/MaterialSec";
import Category from "../Components/Home-page/Category";
import TopCategory from "../Components/Home-page/TopCategory";
import Supply from "../Components/Home-page/Supply";
import BrandSlider from "../Components/Home-page/BrandSlider";
import { useLocation } from "react-router-dom";

function HomeConnect() {
  const routePath = useLocation();
  const onTop = () => {
    window.scrollTo(0, 0);
  };
  useEffect(() => {
    onTop();
  }, [routePath]);



  return (
    <div>
      <BannerSec />
      <NewArrival />
      <MaterialSec />
      <Category />
      <TopCategory  />
      <Supply />
      <BrandSlider />
    </div>
  );
}

export default HomeConnect;
