import React, { useEffect, useState } from 'react'
import Categoryads from "../../assets/new-images/Home-Category/category-ads.jpg"
import { apiurl } from '../../Constants';
import axios from 'axios';

const Category = () => {




    const [datas, setDatas] = useState({});
    const [categories, setCategories] = useState([]);

    
    useEffect(() => {
        // Define an async function to fetch data
        const fetchData = async () => {
            try {
                const response = await axios.post(apiurl + 'centercategories');

                setDatas(response.data.datas); // Extract and store the 'datas' field
                setCategories(response.data.records); // Extract and store the 'setCategories' field
            } catch (err) {
                console.log(`the Error is ${err}`);
            }
        };

        fetchData();
    }, []);



    return (
        <section>

            <div className="full-row pt-5 pb-5 home-category-bg">

                <div className="container">

                    <div className="row justify-content-center">

                        <div className="col-xxl-5 col-xl-7 col-lg-9">

                            <div className="text-center mb-40">

                                <h3 className="text-center mb-4">Categories</h3>

                            </div>

                        </div>

                    </div>

                    <div className="row g-0">

                        <div className="col-lg-4 col-md-12">

                            <div className="banner-wrapper hover-img-zoom banner-one custom-class-133">

                                <div className="banner-image overflow-hidden transation"><img src={`${datas.center_category_image}`} alt="Banner Image" /></div>

                                <div className="banner-content position-absolute">

                                    <div className="text-uppercase text-primary font-500 font-small">{datas.center_category_title}</div>

                                    <h3 className="my-2">{datas.center_category_description}</h3>

                                    <div className="font-500 sale-upto">

                                        <span className="text-secondary">Sale Up To</span>

                                        <span className="ms-2">30% Off</span>

                                    </div>

                                    <a href="javascript:void(0);" className="btn btn-1 btn-outline-secondary ">Enquiry Now</a>

                                </div>

                            </div>

                        </div>

                        <div className="col-lg-8 col-md-12">

                            <div className="row row-cols-lg-3 row-cols-sm-2 row-cols-1 g-0 coustom-categories-banner-2 e-wrapper-absolute e-hover-image-zoom">
                            
                            {categories !== undefined && categories.length > 0 ? categories.map((item) => (

                                <>

                                        <div className="col">

                                            <div className="product type-product">

                                                <div className="product-wrapper">

                                                    <div className="product-image">

                                                        <a href="javascript:void(0);"><img src={`${item.image}`} alt="Product image" /></a>

                                                    </div>

                                                    <div className="product-info">

                                                        <h6 className="product-title"><a href="categories.php">{item.category_name}</a></h6>
                                                     

                                                    </div>

                                                </div>

                                            </div>

                                        </div>
                                        </>
                                    )) : null
                                }

                            </div>

                        </div>

                    </div>

                </div>

            </div>

        </section>
    )
}

export default Category
