import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
import {apiurl} from '../../Constants'
const NewArrival = () => {
  const [newarrivalpro, setNewarrivapro] = useState([]);
  const [datas, setDatas] = useState({});

  useEffect(() => {
    const fetchdatas = async () => {
      await axios
        .post(apiurl + "newarrival")
        .then((res) => {
          setNewarrivapro(res.data.records);
          setDatas(res.data.datas);
        })
        .catch((err) => {
            console.log(`the Error is ${err}`);
        });
    };
    fetchdatas();
  }, []);

  return (
    <div>
      <section>
        <div className="full-row pt-5 pb-5 new-arrival-home">
          <div className="container">
            <div className="row">
              <div className="col-xl-3 col-lg-12 top-cont">
                <div className="text-uppercase text-primary font-medium">
                  {datas.new_arrival_title}
                </div>

                <h3 className="my-3">{datas.new_arrival_description}</h3>

                <a
                  href="javascript:void(0);"
                  className="btn  btn-1 btn-outline-secondary rounded-0"
                >
                  Enquiry Now
                </a>
              </div>

              <div className="col-xl-9 col-lg-12">
                <div className="products product-style-4">
                  <div className="row row-cols-xl-3 row-cols-lg-3 row-cols-md-2 row-cols-1 e-title-hover-primary e-hover-image-zoom e-info-center">
                  
                    {newarrivalpro !== undefined && newarrivalpro.length > 0
                      ? newarrivalpro.map((item) => {
                          return (
                            <div className="col-sm-6 col-md-6 col-lg-4">
                              <div className="product type-product">
                                <div className="product-wrapper">
                                  <div className="product-image">
                                    <Link
                                      to="/Product-details"
                                      className="woocommerce-LoopProduct-link"
                                    >
                                      <img
                                        src={item.image}
                                        alt="Product Image"
                                      />
                                    </Link>

                                    <div className="on-sale">11</div>

                                    <div className="hover-area">
                                      <div className="quickview-button">
                                        <a
                                          className="quickview-btn"
                                          href="javascript:void(0);"
                                          data-bs-toggle="tooltip"
                                          data-bs-placement="top"
                                          title=""
                                          data-bs-original-title="Quick View"
                                          aria-label="Quick View"
                                        >
                                          Quick View
                                        </a>
                                      </div>

                                      <div className="wishlist-button">
                                        <a
                                          className="add_to_wishlist"
                                          href="javascript:void(0);"
                                          data-bs-toggle="tooltip"
                                          data-bs-placement="right"
                                          title=""
                                          data-bs-original-title="Add to Wishlist"
                                          aria-label="Add to Wishlist"
                                        >
                                          Wishlist
                                        </a>
                                      </div>

                                      <div className="compare-button">
                                        <a
                                          className="compare button"
                                          href="javascript:void(0);"
                                          data-bs-toggle="tooltip"
                                          data-bs-placement="right"
                                          title=""
                                          data-bs-original-title="Compare"
                                          aria-label="Compare"
                                        >
                                          Compare
                                        </a>
                                      </div>
                                    </div>
                                  </div>

                                  <div className="product-info">
                                    <h3 className="product-title">
                                      <a href="javascript:void(0);">
                                        {item.product_name}
                                      </a>
                                    </h3>

                                    <a
                                      href="javascript:void(0);"
                                      className="btn btn-1 btn-outline-secondary  mt-3"
                                    >
                                      Enquiry Now
                                    </a>
                                  </div>
                                </div>
                              </div>
                            </div>
                          );
                        })
                      : null}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default NewArrival;
