import React from "react";
import Logo from "../../assets/new-images/global-360-logo-.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowUp } from "@fortawesome/free-solid-svg-icons";

import {
  faFacebookF,
  faLinkedin,
  faSquarePinterest,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import ScrollToTop from "react-scroll-to-top";
import { Link } from "react-router-dom";

function Footer() {
  return (
    <div>
      <ScrollToTop
        smooth
        component={<FontAwesomeIcon icon={faArrowUp} />}
        className="scroll icon scroll-top"
      />
      <footer className="full-row footer-bg bg-secondary p-0">
        <div className="container">
          <div className="row row-cols-lg-4 row-cols-sm-2 row-cols-1">
            <div className="col">
              <div className="footer-widget my-5">
                <div className="footer-logo mb-4">
                  <Link to="/">
                    <img src={Logo} alt="Image not found!" />
                  </Link>
                </div>
                <div className="widget-ecommerce-contact">
                  <div className="text-general text-justify mt-20">
                    Our comprehensive range of products and solutions has been
                    vcarefully designed tomeet the evolving needs of today's
                    learners.{" "}
                  </div>
                </div>
              </div>
            </div>
            <div className="col">
              <div className="footer-widget category-widget my-5">
                <h6 className="widget-title mb-sm-4">Trending Products</h6>
                <ul>
                  <li>
                    <a href="javascript:void(0);">Shoes</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">Belts</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">Ties</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">Uniforms</a>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col">
              <div className="footer-widget category-widget my-5">
                <h6 className="widget-title mb-sm-4">Quick Links</h6>
                <ul>
                  <li>
                    <a href="javascript:void(0);">About</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">Support</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">Contact</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">Compare</a>
                  </li>
                  <li>
                    <a href="javascript:void(0);">FAQ</a>
                  </li>
                </ul>
              </div>
            </div>

            <div className="col">
              <div className="footer-widget widget-nav my-5">
                <h6 className="widget-title mb-sm-4">Account Info</h6>

                <ul>
                  <li>
                    <a href="javascript:void(0);">My Account</a>
                  </li>

                  <li>
                    <a href="javascript:void(0);">Track your Order</a>
                  </li>

                  <li>
                    <a href="javascript:void(0);">Customer Service</a>
                  </li>

                  <li>
                    <a href="javascript:void(0);">Returns / Exchange</a>
                  </li>

                  <li>
                    <a href="javascript:void(0);">Product Support</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </footer>

      <div className="full-row copyright footer-botom-bg border-top border-general py-3">
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <span className="sm-mb-10 d-block">
                 2024 Global 360 educare All right reserved
              </span>
            </div>

            <div className="col-md-6">
              <ul className="list-ml-30 d-flex align-items-center justify-content-md-end">
                <li>
                  <a href="javascript:void(0);">
                    <FontAwesomeIcon icon={faFacebookF} />
                  </a>
                </li>

                <li>
                  <a href="javascript:void(0);">
                    <FontAwesomeIcon icon={faTwitter} />
                  </a>
                </li>

                <li>
                  <a href="javascript:void(0);">
                    <FontAwesomeIcon icon={faLinkedin} />
                  </a>
                </li>

                <li>
                  <a href="javascript:void(0);">
                    <FontAwesomeIcon icon={faSquarePinterest} />
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
