import React from 'react';
import User from "../../assets/new-images/profile-pic-student.svg";
import ReCAPTCHA from "react-google-recaptcha";
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleRight, faHeart, faRightFromBracket } from '@fortawesome/free-solid-svg-icons';
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons/faShoppingCart';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

const Login = () => {

    function onChange(value) {
        console.log("Captcha value:", value);
    }

    var wishlist = [
        {
            img: require("../../assets/new-images/wish-list/socks.png"),
            Product: {
                name: "socks",
                vendor: "Socksoho"
            },
            status: "In stock",
        },
        {
            img: require("../../assets/new-images/wish-list/tshirt.png"),
            Product: {
                name: "Sports Jersey",
                vendor: "Puma"
            },
            status: "In stock",
        },
        {
            img: require("../../assets/new-images/wish-list/tshirtwomen.png"),
            Product: {
                name: "Sports T-Shirt (Men)",
                vendor: "Levi's"
            },
            status: "In stock",
        },
    ]
    var orders = [
        {
            img: require("../../assets/new-images/wish-list/socks.png"),
            Product: {
                name: "socks",
                vendor: "Socksoho"
            },
            Quantity: "2",
            orderstatus: " Delivered on May 23",
        },
        {
            img: require("../../assets/new-images/wish-list/tshirt.png"),
            Product: {
                name: "Sports Jersey",
                vendor: "Puma"
            },
            Quantity: "2",
            orderstatus: " Delivered on May 24",
        },
        {
            img: require("../../assets/new-images/wish-list/tshirtwomen.png"),
            Product: {
                name: "Sports T-Shirt (Men)",
                vendor: "Levi's"
            },
            Quantity: "2",
            orderstatus: " Delivered on May 25",
        },
    ]
    return (
        <div className="full-row account-page">
            <div className="container">
                <Tabs>
                    <div className="row account-details">
                        <div className="col-xl-3 col-lg-3 col-md-12 col-12 account-basic-details">
                            <div className="acc-left-sec">
                                <TabList>
                                    <div className='row'>
                                        <Tab className="col-lg-12 cart-collaterals account-cart-page">
                                            <div className="profile-account-sec">
                                                <img src={User} alt="" className='profile-img' />
                                                <div className="user-sec">
                                                    <div className="user-info">Hello,</div>
                                                    <div className="user-name-sec">My Account</div>
                                                </div>
                                            </div>
                                        </Tab>
                                        <Tab className="col-lg-12 cart-collaterals account-cart-page">
                                            <div className="account-list-btn">
                                                <div className="btn-list-stu">
                                                    <FontAwesomeIcon className='login-icons' icon={faHeart} />
                                                    <div className="user-name-sec">Whish List</div>
                                                </div>
                                                <FontAwesomeIcon className='login-icons' icon={faAngleRight} />
                                            </div>
                                        </Tab>
                                        <Tab className="col-lg-12 cart-collaterals account-cart-page">
                                            <div className="account-list-btn">
                                                <div className="btn-list-stu">
                                                    <FontAwesomeIcon className='login-icons' icon={faShoppingCart} />
                                                    <div className="user-name-sec">Orders</div>
                                                </div>
                                                <FontAwesomeIcon className='login-icons' icon={faAngleRight} />
                                            </div>
                                        </Tab>
                                        <div className="col-lg-12 cart-collaterals account-cart-page logout-tp">
                                            <Link to="/" className="account-list-btn">
                                                <div className="btn-list-stu">
                                                    <FontAwesomeIcon className='login-icons' icon={faRightFromBracket} />
                                                    <div className="user-name-sec">Logout </div>
                                                </div>
                                                <FontAwesomeIcon className='login-icons' icon={faAngleRight} />
                                            </Link>
                                        </div>
                                    </div>
                                </TabList>
                            </div>
                        </div>
                        <div className="col-xl-9 col-lg-9 col-md-12 col-sm-12 col-12 p-0">
                            <TabPanel>
                                <form className="woocommerce-cart-form" action="javascript:void(0);">
                                    <h5 className='mx-3'>Profile Information</h5>
                                    <div className="container p-md-2 p-0">
                                        <div className="row">
                                            <div className="col-md-6 col-sm-6 col-12">
                                                <div className="row p-0 my-1 mx-2">
                                                    <label className="col-12" for="Name"><b>Name</b> <span >*</span></label>
                                                    <input className="col-md-10  col-sm-12 video-form prfile-info-sec valid" type="Name" placeholder="Enter Name" name="Name" id="Name" required="" />
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-sm-6 col-12">
                                                <div className="row p-0 my-1 mx-2">
                                                <label className="col-12" for="Email"><b>Email</b> <span >*</span></label>
                                                    <input className="col-md-10 col-sm-12 col-sm-6 video-form prfile-info-sec valid" type="email" placeholder="Enter Email" name="Email" id="Email" required="" />
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-sm-6 col-12">
                                                <div className="row p-0 my-1 mx-2">
                                                    <label className="col-12" for="mobile"><b>Mobile</b> <span >*</span></label>
                                                    <input className="col-md-10  col-sm-12 video-form prfile-info-sec valid" type="text" onkeypress="return onlyNumberKey(event)" minlength="10" maxlength="13" placeholder="Enter Mobile" name="mobile" id="mobile" required="" />
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-sm-6 col-12">
                                                <div className="row p-0 my-1 mx-2">
                                                    <label className="col-12" for="gender"><b>City</b> <span>*</span></label>
                                                    <select className="col-md-10  col-sm-12 video-form prfile-info-sec valid" type="text" placeholder="Select Gender" name="gender" id="gender" required="">
                                                        <option value="">Select City</option>
                                                        <option value="">City 1</option>
                                                        <option value="">City 2</option>
                                                        <option value="">City 3</option>
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-sm-6 col-12 ">
                                                <div className="row p-0 my-1 mx-2">
                                                    <label className="col-12" for="mobile"><b>Subject</b> <span >*</span></label>
                                                    <input className="col-md-10 col-sm-12 video-form prfile-info-sec valid" type="text" onkeypress="return onlyNumberKey(event)" minlength="10" maxlength="13" placeholder="Enter subject" name="mobile" id="mobile" required="" />
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-sm-6 col-12">
                                                <div className="row p-0 my-1 mx-2">
                                                    <label className="col-12" for="mobile"><b>Message</b> <span >*</span></label>
                                                    <textarea className="col-md-10 col-sm-12 video-form prfile-info-sec valid" type="text" onkeypress="return onlyNumberKey(event)" minlength="10" maxlength="13" placeholder="Message" name="mobile" id="mobile" required="" />
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-12">
                                                <div className="row p-0 my-1 mx-2">
                                                    <ReCAPTCHA sitekey={'6Lc7XCgdAAAAAJH97tyf50fw9E4g-JQSk4eXIKJZ'} className="g-recaptcha" onChange={onChange} />
                                                </div>
                                            </div>
                                            <div className="col-md-6 col-12">
                                                <button href="javascript:void(0);" className="btn btn-1 btn-outline-secondary  mt-3">submit</button>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </TabPanel>
                            <TabPanel>
                                <form className="woocommerce-cart-form account-table-main" action="#" method="post">
                                    <table className="shop_table cart account-table">
                                        <tbody>
                                            <tr>
                                                <th className="product-thumbnail" colSpan="2">&nbsp;</th>
                                                <th className="product-name">Product</th>
                                                <th className="product-subtotal">Stock Status</th>
                                                <th className="product-price"></th>
                                            </tr>
                                            {wishlist.map((item) => {
                                                return (
                                                    <tr className="woocommerce-cart-form__cart-item cart_item">
                                                        <td className="product-thumbnail" colSpan="2">
                                                            <a href="#"><img src={item.img} alt="Product image" /></a>
                                                        </td>
                                                        <td className="product-name">
                                                            <a href="#">{item.Product.name}</a>
                                                            <dl className="variation">
                                                                <dt className="variation-Vendor">Vendor:</dt>
                                                                <dd className="variation-Vendor">{item.Product.vendor}</dd>
                                                            </dl>
                                                        </td>
                                                        <td className="product-quantity">
                                                            <div className="quantity">
                                                                <div className="rating-wrap">
                                                                    <a href="#"><span> {item.status}</span></a>
                                                                </div>
                                                            </div>
                                                        </td>
                                                        <td className="product-subtotal">
                                                            <Link to="/Student-Needs" className="btn btn-1 btn-outline-secondary  mt-3">View More</Link>
                                                            <a href="javascript:void(0);" className="btn btn-1 cancel-btn btn-outline-secondary  mt-3 mx-2">x</a>
                                                        </td>
                                                    </tr>
                                                )
                                            })}
                                        </tbody>
                                    </table>
                                </form>
                            </TabPanel>
                            {/*orders tab*/}
                            <TabPanel>
                                <form className="woocommerce-cart-form account-table-main" action="#" method="post">
                                    <table className="shop_table cart account-table">
                                        <tbody>
                                            <tr>
                                                <th className="product-thumbnail" colSpan="2">&nbsp;</th>
                                                <th className="product-name">Product</th>
                                                <th className="product-name">Quantity</th>
                                                <th className="product-subtotal">Order Status</th>
                                                <th className="product-price"></th>
                                            </tr>
                                            {orders.map((item) => {
                                                return (
                                                    <tr className="woocommerce-cart-form__cart-item cart_item">
                                                        <td className="product-thumbnail" colSpan="2">
                                                            <a href="#"><img src={item.img} alt="Product image" /></a>
                                                        </td>
                                                        <td className="product-name">
                                                            <a href="#">{item.Product.name}</a>
                                                            <dl className="variation">
                                                                <dt className="variation-Vendor">Vendor:</dt>
                                                                <dd className="variation-Vendor">{item.Product.vendor}</dd>
                                                            </dl>
                                                        </td>
                                                        <td className="product-quantity">
                                                            <div className="quantity">
                                                                <div className="rating-wrap">
                                                                    <a href="#"><span> {item.Quantity}</span></a>
                                                                </div>
                                                            </div>
                                                        </td>
                                                        <td className="product-quantity">
                                                            <div className="quantity">
                                                                <div className="rating-wrap">
                                                                    <a href="#" className='greenstock'><div className='round-order-green'></div><span>{item.orderstatus}</span></a>
                                                                </div>
                                                            </div>
                                                        </td>
                                                        <td className="product-subtotal">
                                                            <Link to="/Student-Needs" className="order-btn btn btn-1 btn-outline-secondary  mt-3">View More</Link>
                                                            <a href="javascript:void(0);" className="btn btn-1 cancel-btn btn-outline-secondary  mt-3 mx-2">x</a>
                                                        </td>
                                                    </tr>
                                                )
                                            })}

                                        </tbody>
                                    </table>
                                </form>
                            </TabPanel>
                            {/*orders tab*/}
                        </div>
                    </div>
                </Tabs>
            </div>
        </div>
    )
}

export default Login
